<template>
  <div class="content-box">
    <h2 class="demo-title">BackTop </h2>
    <p class="demo-introduction"></p>
    <!-- <div class="demo-waring">
      <p>
        
        <code>padding</code>
        <code>margin</code>
      </p>
    </div>-->

    <!-- DEMO -->
    <shn-el-demo-block :height="82" :title="''">
      <template v-slot:demo>
        <p class="demo-code-en-p">Scroll down to see the bottom-right gray button.</p>
        <p class="demo-code-cn-p"></p>
        <shn-backtop :visibilityHeight="100"/>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(simple.code.html)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block :height="125" :introduction="''" :title="''">
      <template v-slot:demo>
        <p class="demo-code-cn-p"></p>
        <shn-backtop :visibilityHeight="0">
          <div style="font-size:14px;margin-top:20px;cursor: pointer;"></div>
        </shn-backtop>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(customize.code.html)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- API -->
    <div>
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="api" :type="'demo'"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'shn-backtop-demo',
  data() {
    return {
      simple: {
        code: {
          html: `
          <shn-backtop :visibilityHeight="100"/>
          `
        }
      },
      customize: {
        code: {
          html: `
          <shn-backtop :visibilityHeight="0">
            <div style="font-size:14px;margin-top:20px;cursor: pointer;"></div>
          </shn-backtop>
          `
        }
      },
      api: [
        {
          parameter: 'target',
          description: ',',
          dataTypes: 'String',
          optional: '',
          default: 'window'
        },
        {
          parameter: 'visibilityHeight',
          description: '0',
          dataTypes: 'Number',
          optional: '',
          default: '500'
        }
      ]
    }
  },
  methods: {}
}
</script>
