<template>
  <div class="content-box">
    <h2 class="demo-title">WidgetModal </h2>

    <!-- DEMO -->
    <shn-el-demo-block :height="252" :title="''" id="widget-modal-simple">
      <template v-slot:demo>
        <p class="demo-code-en-p">see the bottom left modal.</p>
        <p class="demo-code-cn-p"></p>
        <shn-widget-modal :visible.sync="simple.show" title="">
          <p></p>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
        </shn-widget-modal>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(simple.code.html)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="api" :type="'demo'" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'shn-widget-modal-demo',
  data() {
    return {
      simple: {
        code: {
          html: `
          <shn-widget-modal :visible.sync="simple.show" title="">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </shn-widget-modal>
          `
        },
        show: true
      },
      api: [
        {
          parameter: 'visible',
          description: ' Modal .sync 	',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'title',
          description: 'Modal',
          dataTypes: 'String',
          optional: '',
          default: 'Title'
        },
        {
          parameter: 'width',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '450'
        },
        {
          parameter: 'height',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '200'
        },
        {
          parameter: 'isDraggable',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'isResizable',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'aspectRatio',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        }
      ]
    }
  },
  methods: {}
}
</script>
