<template>
  <div class="content-box">
    <h2 class="demo-title">UploadImg </h2>
    <p class="demo-introduction"></p>

    <div class="demo-tip">
      <p>vue-croppergithub</p>
      <p>
        <a
          href="https://github.com/xyxiao001/vue-cropper"
          target="_blank"
        >https://github.com/xyxiao001/vue-cropper</a>
        <img
          alt
          class="github-img"
          href="https://github.com/xyxiao001/vue-cropper"
          src="https://img.shields.io/github/stars/xyxiao001/vue-cropper.svg?style=social&label=Star&maxAge=2592000"
        />
      </p>
    </div>

    <shn-anchor>
      <shn-anchor-link href="upload-img-simple" title="" />
      <shn-anchor-link href="upload-img-cropper" title="" />
      <shn-anchor-link href="api" title="API" />
    </shn-anchor>

    <!-- DEMO -->
    <shn-el-demo-block :height="250" :title="''" id="upload-img-simple">
      <template v-slot:demo>
        <shn-upload-img @change="handelChange" addText="" v-model="simple.list" />
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(simple.code.html)}}</code>
        <code class="javascript">{{fCode(simple.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block :height="543" :title="''" id="upload-img-cropper">
      <template v-slot:demo>
        <shn-upload-img
          :cropper="true"
          :fixedNumber="[1.4,2]"
          :height="300"
          :max="2"
          :width="300"
          @change="handelChange"
          addText="/"
          cropType="blob"
          v-model="cropper.list"
        />
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(cropper.code.html)}}</code>
        <code class="javascript">{{fCode(cropper.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="api" :type="'demo'" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'shn-upload-img-demo',
  data() {
    return {
      simple: {
        code: {
          html: `
          <shn-upload-img addText="" v-model="list"/>
          `,
          javascript: `
          export default {
            data(){
              return{
                list:[]
              }
            }
          }
          `
        },
        list: ['https://shnhz.github.io/shn-ui/img/Koala.jpg']
      },
      cropper: {
        code: {
          html: `
          <shn-upload-img
            :cropper="true"
            @change="handelChange"
            addText="/"
            cropType="blob"
            v-model="cropper.list"
            :width="300"
            :height="300"
            :fixedNumber="[1.4,2]"
            :max="2"
          />
          `,
          javascript: `
          export default {
            data(){
              return{
                list:[]
              }
            },
            methods: {
              handelChange(data) {
                console.log(data)
              }
            }
          }
          `
        },
        list: ['https://shnhz.github.io/shn-ui/img/Koala.jpg']
      },
      api: [
        {
          parameter: 'value / v-model',
          description: '',
          dataTypes: 'Array',
          optional: '',
          default: ''
        },
        {
          parameter: 'addText',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'max',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: ''
        },
        {
          parameter: 'width',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '104'
        },
        {
          parameter: 'height',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '104'
        },
        {
          parameter: 'view',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'cropper',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'fixed',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'fixedNumber',
          description: '',
          dataTypes: 'Array',
          optional: '[, ]',
          default: '[1, 1]'
        },
        {
          parameter: 'outputSize',
          description: '',
          dataTypes: 'Number',
          optional: '0.1 - 1',
          default: '1'
        },
        {
          parameter: 'cropType',
          description: '',
          dataTypes: 'String',
          optional: 'base64 / blob',
          default: 'base64'
        },
        {
          parameter: '',
          description: 'disabled',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        }
      ]
    }
  },
  methods: {
    handelChange(data) {
      console.log(data)
    }
  }
}
</script>
<style lang="scss" scoped>
</style>

