<template>
  <div class="content-box">
    <h2 class="demo-title">Statistic </h2>
    <p class="demo-introduction"></p>

    <shn-anchor>
      <shn-anchor-link href="statistic-simple" title="" />
      <shn-anchor-link href="statistic-unit" title="" />
      <shn-anchor-link href="statistic-prefix" title="" />
      <shn-anchor-link href="statistic-fontsize" title="" />
      <shn-anchor-link href="statistic-decimalNum" title="" />
      <shn-anchor-link href="statistic-percent" title="" />
      <shn-anchor-link href="api" title="API" />
    </shn-anchor>

    <!-- DEMO -->
    <shn-el-demo-block
      :height="250"
      :introduction="''"
      :title="''"
      id="statistic-simple"
    >
      <template v-slot:demo>
        <shn-statistic v-model="simple.value" />
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(simple.code.html)}}</code>
        <code class="javascript">{{fCode(simple.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block :height="250" :introduction="''" :title="''" id="statistic-unit">
      <template v-slot:demo>
        <shn-statistic :unit="''" v-model="unit.value" />
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(unit.code.html)}}</code>
        <code class="javascript">{{fCode(unit.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block :height="250" :introduction="''" :title="''" id="statistic-prefix">
      <template v-slot:demo>
        <shn-statistic :prefix="prefix.prefix" v-model="prefix.value" style="color:#67C23A" :decimalNum="0"/>
        <shn-statistic :prefix="prefix.prefix_2" style="margin-left:10px;color:#F56C6C" v-model="prefix.value" :decimalNum="0"/>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(prefix.code.html)}}</code>
        <code class="javascript">{{fCode(prefix.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block
      :height="250"
      :introduction="''"
      :title="''"
      id="statistic-fontsize"
    >
      <template v-slot:demo>
        <shn-statistic
          :decimalFontSize="24"
          :intFontSize="16"
          :unit="''"
          :unitFontSize="30"
          v-model="fontSize.value"
        />
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(fontSize.code.html)}}</code>
        <code class="javascript">{{fCode(fontSize.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block
      :height="250"
      :introduction="''"
      :title="''"
      id="statistic-decimalNum"
    >
      <template v-slot:demo>
        <shn-statistic :decimalNum="1" :groupSeparator="false" v-model="decimalNum.value" />
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(decimalNum.code.html)}}</code>
        <code class="javascript">{{fCode(decimalNum.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block
      :height="250"
      :introduction="'100'"
      :title="''"
      id="statistic-percent"
    >
      <template v-slot:demo>
        <shn-statistic :unit="'%'" percent v-model="percent.value" />
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(percent.code.html)}}</code>
        <code class="javascript">{{fCode(percent.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="api" :type="'demo'" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'shn-statistic-demo',
  data() {
    return {
      //DEMO
      simple: {
        code: {
          html: `
          <shn-statistic v-model="value"/>
          `,
          javascript: `
          export default {
            data() {
              return {
                value:'800547.20'
              }
            }
          }
          `
        },
        value: '800547.20'
      },
      //DEMO
      unit: {
        code: {
          html: `
          <shn-statistic v-model="unit.value" :unit="''"/>
          `,
          javascript: `
          export default {
            data() {
              return {
                value:'800547.20'
              }
            }
          }
          `
        },
        value: '800547.20'
      },
      //DEMO
      prefix: {
        code: {
          html: `
          <shn-statistic v-model="prefix.value" :prefix="prefix.prefix" style="color:#67C23A" :decimalNum="0"/>
          <shn-statistic v-model="prefix.value" :prefix="prefix.prefix_2" style="color:#F56C6C" :decimalNum="0"/>
          `,
          javascript: `
          export default {
            data() {
              return {
                value:'8',
                prefix:'<i class="shni shn-arrowup" />',
                prefix_2: '<i class="shni shn-arrowdown" />'
              }
            }
          }
          `
        },
        value: '8',
        prefix: '<i class="shni shn-arrowup" />',
        prefix_2: '<i class="shni shn-arrowdown" />'
      },
      //DEMO
      fontSize: {
        code: {
          html: `
          <shn-statistic v-model="fontSize.value" :intFontSize="16" :decimalFontSize="24" :unitFontSize="30" :unit="''"/>
          `,
          javascript: `
          export default {
            data() {
              return {
                value:'800547.20'
              }
            }
          }
          `
        },
        value: '800547.20'
      },
      //DEMO
      decimalNum: {
        code: {
          html: `
          <shn-statistic v-model="decimalNum.value" :decimalNum="1" :groupSeparator="false" />
          `,
          javascript: `
          export default {
            data() {
              return {
                value:'800547.20'
              }
            }
          }
          `
        },
        value: '800547.20'
      },
      //DEMO
      percent: {
        code: {
          html: `
          <shn-statistic v-model="percent.value" :unit="'%'" percent/>
          `,
          javascript: `
          export default {
            data() {
              return {
                value:'800547.28489'
              }
            }
          }
          `
        },
        value: '800547.28489'
      },
      api: [
        {
          parameter: 'value / v-model',
          description: '',
          dataTypes: 'Number / String',
          optional: '',
          default: '0'
        },
        {
          parameter: 'prefixFontSize',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '18'
        },
        {
          parameter: 'intFontSize',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '24'
        },
        {
          parameter: 'decimalFontSize',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '16'
        },
        {
          parameter: 'unitFontSize',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '16'
        },
        {
          parameter: 'prefix',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'unit',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'groupSeparator',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'decimalNum',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '2'
        },
        {
          parameter: 'prefixFontClass',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'intFontClass',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'decimalFontClass',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'unitFontClass',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'percent',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        }
      ]
    }
  },
  methods: {}
}
</script>
<style lang="scss" scoped>
</style>

