<template>
  <div class="content-box">
    <h2 class="demo-title">Input </h2>
    <p class="demo-introduction"></p>
    <div class="demo-waring">
      <p>Input  Vue </p>
    </div>

    <shn-anchor>
      <shn-anchor-link href="input-simple" title="" />
      <shn-anchor-link href="input-pattern" title="" />
      <shn-anchor-link href="input-disabled" title="" />
      <shn-anchor-link href="api" title="API" />
    </shn-anchor>

    <!-- DEMO -->
    <shn-el-demo-block :height="312" :title="''" id="input-simple">
      <template v-slot:demo>
        <shn-input v-model="simple.input" />
        <span style="margin-left:20px">{{simple.input}}</span>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(simple.code.html)}}</code>
        <code class="javascript">{{fCode(simple.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block :height="375" :title="''" id="input-pattern">
      <template v-slot:demo>
        <div style="padding:0 0 15px">
          <div style="display:inline-block;width:100px;font-size:14px">line</div>
          <shn-input :pattern="'line'" v-model="pattern.line_input" />
          <span style="margin-left:20px">{{pattern.line_input}}</span>
        </div>
        <div style="padding:0 0 15px">
          <div style="display:inline-block;width:100px;font-size:14px">frame</div>
          <shn-input :pattern="'frame'" v-model="pattern.frame_input" />
          <span style="margin-left:20px">{{pattern.frame_input}}</span>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(pattern.code.html)}}</code>
        <code class="javascript">{{fCode(pattern.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block :height="375" :title="''" id="input-disabled">
      <template v-slot:demo>
        <div style="padding:0 0 15px">
          <div style="display:inline-block;width:100px;font-size:14px">line</div>
          <shn-input :pattern="'line'" disabled v-model="disabled.line_input" />
          <span style="margin-left:20px">{{disabled.line_input}}</span>
        </div>
        <div style="padding:0 0 15px">
          <div style="display:inline-block;width:100px;font-size:14px">frame</div>
          <shn-input :pattern="'frame'" disabled v-model="disabled.frame_input" />
          <span style="margin-left:20px">{{disabled.frame_input}}</span>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(disabled.code.html)}}</code>
        <code class="javascript">{{fCode(disabled.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="api" :type="'demo'" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'shn-input-demo',
  data() {
    return {
      //DEMO
      simple: {
        code: {
          html: `
          <template>
            <shn-input v-model="input"/>
            <span style="margin-left:20px">{{input}}</span>
          </template>
          `,
          javascript: `
          export default {
            data() {
              return {
                input:''
              }
            }
          }
          `
        },
        input: ''
      },
      pattern: {
        code: {
          html: `
          <template>
            <shn-input v-model="line_input" :pattern="'line'"/>
            <span style="margin-left:20px">{{line_input}}</span>
            <shn-input v-model="frame_input" :pattern="'frame'" />
            <span style="margin-left:20px">{{frame_input}}</span>
          </template>
          `,
          javascript: `
          export default {
            data() {
              return {
                line_input:'',
                frame_input:''
              }
            }
          }
          `
        },
        line_input: '',
        frame_input: ''
      },
      disabled: {
        code: {
          html: `
          <template>
            <shn-input v-model="line_input" :pattern="'line'" disabled/>
            <span style="margin-left:20px">{{line_input}}</span>
            <shn-input v-model="frame_input" :pattern="'frame'" disabled/>
            <span style="margin-left:20px">{{frame_input}}</span>
          </template>
          `,
          javascript: `
          export default {
            data() {
              return {
                line_input:'',
                frame_input:''
              }
            }
          }
          `
        },
        line_input: '',
        frame_input: ''
      },
      api: [
        {
          parameter: 'value / v-model',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'pattern',
          description: '',
          dataTypes: 'String',
          optional: 'line / frame',
          default: 'line'
        },
        {
          parameter: 'type',
          description: '',
          dataTypes: 'String',
          optional: 'text input',
          default: 'text'
        },
        {
          parameter: 'placeholder',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'height',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '40'
        },
        {
          parameter: 'disabled',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'animation',
          description: 'line',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'mode',
          description: '',
          dataTypes: 'String',
          optional: 'horizontal / vertical',
          default: 'horizontal'
        },
        {
          parameter: 'suffix-icon',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'prefix-icon',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        }
      ]
    }
  },
  methods: {}
}
</script>
<style lang="scss" scoped>
</style>

