<template>
  <div class="content-box">
    <shn-backtop :visibilityHeight="300" />

    <h2 class="demo-title">VueWordCloud </h2>

    <div class="demo-tip">
      <p>SeregPiegithub</p>
      <p>
        <a
          href="https://github.com/SeregPie/VueWordCloud"
          target="_blank"
        >https://github.com/SeregPie/VueWordCloud</a>
        <img
          alt
          class="github-img"
          href="https://github.com/SeregPie/VueWordCloud"
          src="https://img.shields.io/github/stars/SeregPie/VueWordCloud.svg?style=social&label=Star&maxAge=2592000"
        />
      </p>
    </div>

    <div class="demo-tip">
      <p>demo</p>
      <p>
        <a
          href="https://seregpie.github.io/VueWordCloud/"
          target="_blank"
        >https://seregpie.github.io/VueWordCloud/</a>
      </p>
    </div>

    <!--  -->
    <h3 class="demo-table-title"></h3>
    <p class="demo-introduction"> npm  webpack </p>
    <div class="demo-code-block">
      <p>npm install vuewordcloud</p>
      <shn-divider />
      <p>main.js</p>
      <p>import VueWordCloud from 'vuewordcloud'</p>
      <p>Vue.component(VueWordCloud.name, VueWordCloud)</p>
      <shn-divider />
      <p></p>
      <p>import VueWordCloud from 'vuewordcloud'</p>
      <p>components: {</p>
      <p style="padding-left:10px">[VueWordCloud.name]: VueWordCloud</p>
      <p>}</p>
    </div>

    <shn-anchor>
      <shn-anchor-link href="vue-word-clound-simple" title="" />
      <shn-anchor-link href="vue-word-clound-other" title="" />
      <shn-anchor-link href="api" title="API" />
      <shn-anchor-link href="event" title="" />
    </shn-anchor>

    <!-- DEMO -->
    <shn-el-demo-block
      :height="168"
      :title="''"
      id="vue-word-clound-simple"
      introduction=""
    >
      <template v-slot:demo>
        <div style="width:100%;height:500px">
          <vue-word-cloud
            :color="([, weight]) => weight > 10 ? 'DeepPink' : weight > 5 ? 'RoyalBlue' : 'Indigo'"
            :words="[['romance', 19], ['horror', 3], ['fantasy', 7], ['adventure', 3]]"
            font-family="Roboto"
          />
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(simple.code.html)}}</code>
        <!-- <code class="javascript">{{fCode(simple.code.javascript)}}</code> -->
      </template>
    </shn-el-demo-block>
    <!--  -->
    <shn-el-demo-block
      :arrow-animation="false"
      :height="816"
      :title="''"
      id="vue-word-clound-other"
    >
      <template v-slot:demo>
        <div style="width:100%;height:500px">
          <vue-word-cloud
            :color="([, weight]) => other.colors[Math.ceil(Math.random() * 5 - 1)]"
            :font-size-ratio="5"
            :words="other.code.words"
            enter-animation="bounceIn"
            font-family="SimSun"
            font-weight="800"
            leave-animation="bounceOut"
          />
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(other.code.html)}}</code>
        <code class="javascript">{{fCode(other.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>
    <shn-el-demo-block
      :arrow-animation="false"
      :height="942"
      :title="''"
      id="vue-word-clound-other"
    >
      <template v-slot:demo>
        <div style="width:100%;height:500px">
          <vue-word-cloud
            :color="([, weight]) => other.colors[Math.ceil(Math.random() * 5 - 1)]"
            :font-size-ratio="5"
            :words="other.code_2.words"
            enter-animation="bounceIn"
            font-family="SimSun"
            font-weight="800"
            leave-animation="bounceOut"
            rotation-unit="deg"
          />
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(other.code_2.html)}}</code>
        <code class="javascript">{{fCode(other.code_2.javascript)}}</code>
      </template>
    </shn-el-demo-block>
    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="api" :type="'demo'" />
    </div>
    <!--  -->
    <div id="event">
      <h3 class="demo-table-title"></h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="event" :type="'demo'" />
    </div>
  </div>
</template>

<script>
import VueWordCloud from 'vuewordcloud'

export default {
  name: 'vue-word-cloud-demo',
  components: {
    [VueWordCloud.name]: VueWordCloud
  },
  data() {
    return {
      words: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        'shn-ui',
        'QQ',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      simple: {
        code: {
          html: `
          <vue-word-cloud
            :color="([, weight]) => weight > 10 ? 'DeepPink' : weight > 5 ? 'RoyalBlue' : 'Indigo'"
            :words="[['romance', 19], ['horror', 3], ['fantasy', 7], ['adventure', 3]]"
            font-family="Roboto"
          />
          `
        }
      },
      other: {
        colors: ['#ffd077', '#3bc4c7', '#3a9eea', '#ff4e69', '#461e47'],
        code: {
          html: `
              <div style="width:100%;height:500px">
                <vue-word-cloud
                  :color="([, weight]) => other.colors[Math.ceil(Math.random() * 5 - 1)]"
                  :font-size-ratio="5"
                  :words="other.words"
                  enter-animation="bounceIn"
                  font-family="SimSun"
                  font-weight="800"
                  leave-animation="bounceOut"
                />
              </div>
          `,
          javascript: `
            export default {
              data() {
                return {
                  word:[
                    '',
                    '',
                    ...
                  ],
                  words:[]
                }
              },
              mounted(){
                this.words = this.getWord()
              },
              methods: {
                getWord() {
                  for (let i = 0; i < this.word.length; i++) {
                    this.word[i] = [this.word[i], Math.ceil(Math.random() * 30)]
                  }

                  return this.word
                }
              }
            }
          `,
          words: []
        },
        code_2: {
          html: `
              <div style="width:100%;height:500px">
                <vue-word-cloud
                    :color="([, weight]) => other.colors[Math.ceil(Math.random() * 5 - 1)]"
                    :font-size-ratio="5"
                    :words="other.words"
                    enter-animation="bounceIn"
                    font-family="SimSun"
                    font-weight="800"
                    leave-animation="bounceOut"
                    rotation-unit="deg"
                />
              </div>
          `,
          javascript: `
            export default {
              data() {
                return {
                  word:[
                    '',
                    '',
                    ...
                  ],
                  words:[]
                }
              },
              mounted(){
                this.words = this.getWord()
              },
              methods: {
                getWord() {
                  let word = JSON.parse(JSON.stringify(this.words))
                  for (let i = 0; i < word.length; i++) {
                    word[i] = {
                      text: word[i],
                      weight: Math.ceil(Math.random() * 30),
                      rotation: Math.ceil(Math.random() * 12) * 30
                    }
                  }

                  return word
                }
              }
            }
          `,
          words: []
        }
      },
      api: [
        {
          parameter: 'animation-duration',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '1000'
        },
        {
          parameter: 'animation-easing',
          description: '',
          dataTypes: 'String',
          optional:
            'ease / linear / ease-in / ease-out / ease-in-out / cubic-bezier(.1,.7,1,.1)',
          default: 'ease'
        },
        {
          parameter: 'animation-overlap',
          description: '',
          dataTypes: 'Number',
          optional: '[ 1 - 5 ]',
          default: '1'
        },
        {
          parameter: 'color',
          description: '',
          dataTypes: 'String / Function',
          optional: '',
          default: 'Black'
        },
        {
          parameter: 'create-canvas',
          description: 'Creates a new Canvas instance.',
          dataTypes: 'Function',
          optional: '',
          default: ''
        },
        {
          parameter: 'create-worker',
          description: 'Creates a new Worker instance.',
          dataTypes: 'Function',
          optional: '',
          default: ''
        },
        {
          parameter: 'enter-animation',
          description: 'The enter animation.',
          dataTypes: 'Object / String',
          optional: '',
          default: ''
        },
        {
          parameter: 'font-family',
          description: '',
          dataTypes: 'String / Function',
          optional: '',
          default: 'serif'
        },
        {
          parameter: 'font-size-ratio',
          description:
            '5551/5',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'font-style',
          description: '',
          dataTypes: 'String / Function',
          optional: '',
          default: 'normal'
        },
        {
          parameter: 'font-variant',
          description: '',
          dataTypes: 'String / Function',
          optional: '',
          default: 'normal'
        },
        {
          parameter: 'font-weight',
          description: '',
          dataTypes: 'String / Function',
          optional: '',
          default: 'normal'
        },
        {
          parameter: 'leave-animation',
          description: 'The leave animation.',
          dataTypes: 'Object / String',
          optional: '',
          default: ''
        },
        {
          parameter: 'load-font',
          description: 'Loads the font.',
          dataTypes: 'Function',
          optional: '',
          default: ''
        },
        {
          parameter: 'rotation-unit',
          description: '',
          dataTypes: 'String / Function',
          optional: 'turn / deg / rad',
          default: 'turn'
        },
        {
          parameter: 'rotation',
          description: 'The default rotation for each word.',
          dataTypes: 'Number/ Function',
          optional: '',
          default: '0'
        },
        {
          parameter: 'spacing',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'text',
          description: '',
          dataTypes: 'String / Function',
          optional: '',
          default: ''
        },
        {
          parameter: 'weight',
          description: '',
          dataTypes: 'Number / Function',
          optional: '',
          default: '1'
        },
        {
          parameter: 'words',
          description: `
                        {text, weight, rotation, rotationUnit, fontFamily, fontStyle, fontVariant, fontWeight, color}
                        [text, weight]
                        text`,
          dataTypes: 'Array',
          optional: '',
          default: '[]'
        }
      ],
      event: [
        {
          parameter: '@update',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        }
      ]
    }
  },
  mounted() {
    this.other.code.words = this.getWord()
    this.other.code_2.words = this.getWordCode2()
  },
  methods: {
    getWord() {
      let word = JSON.parse(JSON.stringify(this.words))
      for (let i = 0; i < word.length; i++) {
        word[i] = [word[i], Math.ceil(Math.random() * 30)]
      }

      return word
    },
    getWordCode2() {
      let word = JSON.parse(JSON.stringify(this.words))
      for (let i = 0; i < word.length; i++) {
        word[i] = {
          text: word[i],
          weight: Math.ceil(Math.random() * 30),
          rotation: Math.ceil(Math.random() * 12) * 30
        }
      }

      return word
    }
  }
}
</script>
