<template>
  <div class="content-box">
    <h2 class="demo-title">FooterToolbar </h2>
    <p class="demo-introduction"></p>

    <!-- DEMO -->
    <shn-el-demo-block :height="522" :title="''">
      <template v-slot:demo>
        <p class="demo-code-en-p">see the bottom bar.</p>
        <p class="demo-code-cn-p"></p>
        <shn-footertoolbar
          :cancelButtonText="simple.cancelButtonText"
          :visibilityHeight="200"
          @cancel="cancel"
          @confirm="confirm"
        ></shn-footertoolbar>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(simple.code.html)}}</code>
        <code class="javascript">{{fCode(simple.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- API -->
    <div>
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="api" :type="'demo'"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'shn-footertoolbar-demo',
  data() {
    return {
      simple: {
        code: {
          html: `
          <shn-footertoolbar
            :cancelButtonText="cancelButtonText"
            :visibilityHeight="50"
            @cancel="cancel"
            @confirm="confirm"
          ></shn-footertoolbar>
          `,
          javascript: `
          export default {
            data() {
              return {
                cancelButtonText:''
              }
            },
            methods: {
              confirm() {
                this.cancelButtonText = ''
              },
              cancel() {
                this.cancelButtonText = 'Cancel'
              }
            }
          }
          `
        },
        cancelButtonText: 'Cancel'
      },
      api: [
        {
          parameter: 'target',
          description: ',',
          dataTypes: 'String',
          optional: '',
          default: 'window'
        },
        {
          parameter: 'visibilityHeight',
          description: '0',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'content',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: 'extra information'
        },
        {
          parameter: 'cancelButtonShow',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'confirmButtonShow',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'cancelButtonText',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: 'Cancel'
        },
        {
          parameter: 'confirmButtonText',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: 'Confirm'
        },
        {
          parameter: '@cancel',
          description: '',
          dataTypes: 'Function',
          optional: '',
          default: ''
        },
        {
          parameter: '@confirm',
          description: '',
          dataTypes: 'Function',
          optional: '',
          default: ''
        }
      ]
    }
  },
  methods: {
    confirm() {
      this.simple.cancelButtonText = ''
    },
    cancel() {
      this.simple.cancelButtonText = 'Cancel'
    }
  }
}
</script>
<style lang="scss" scoped>
</style>

