    <div class="demo-code-block">
      <p>npm install vue-cropper</p>
      <p>yarn add vue-cropper</p>
      <shn-divider />
      <p>main.js</p>
      <p>import VueCropper from 'vue-cropper'</p>
      <p>Vue.use(VueCropper)</p>
      <shn-divider />
      <p></p>
      <p>import { VueCropper } from 'vue-cropper'</p>
      <p>components: {</p>
