            <div class="simple-block">
              <div>
                
                <p>{{ top }}  {{ left }}</p>
                <p>{{ width }}  {{ height }}</p>
              </div>
            </div>
          </vue-drag-resize>
          `,
          javascript: `
          export default {
            data(){
              return{
                width: 100,
                height: 100,
                top: 20,
                left: 20
              }
            },
            methods:{
              resize(newRect) {
                this.simple.width = newRect.width
                this.simple.height = newRect.height
                this.simple.top = newRect.top
                this.simple.left = newRect.left
              }
            }
          }
          `
        },
        width: 100,
        height: 100,
        top: 20,
        left: 20
      },
      axis: {
        code: {
          html: `
          <vue-drag-resize :h="100" :w="100" :x="20" :y="20" axis="x" parentLimitation>
            <div class="simple-block">
              <div>
                <p></p>
                <p></p>
              </div>
            </div>
          </vue-drag-resize>
          `,
          javascript: ``
        }
      },
      api: [
        {
          parameter: 'isActive',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'isDraggable',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'isResizable',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'aspectRatio',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'isResizable',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'w',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '200'
        },
        {
          parameter: 'h',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '200'
        },
        {
          parameter: 'minw',
          description: '',
          dataTypes: 'Number',
          optional: '>0',
          default: '50'
        },
        {
          parameter: 'minh',
          description: '',
          dataTypes: 'Number',
          optional: '>0',
          default: '50'
        },
        {
          parameter: 'x',
          description: 'left',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'y',
          description: 'top',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'z',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: 'auto'
        },
        {
          parameter: 'sticks',
          description: '',
          dataTypes: 'Array',
          optional: '',
          default: ''
        },
        {
          parameter: 'preventActiveBehavior',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'parentLimitation',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'parentW',
          description:
            'parentLimitation=true',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'parenth',
          description:
            ',parentLimitation=true',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'axis',
          description: '',
          dataTypes: 'String',
          optional: 'x / y / both / none',
          default: 'both'
        },
        {
          parameter: 'dragHandle',
          description: 'classname',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'dragCancel',
          description: 'classname',
          dataTypes: 'String',
          optional: '',
          default: ''
        }
      ],
      event: [
        {
          parameter: '@clicked',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@activated',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@resizing',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@resizestop',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@dragging',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@dragstop',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        }
      ]
    }
  },
  methods: {
    resize(newRect) {
      this.simple.width = newRect.width
      this.simple.height = newRect.height
      this.simple.top = newRect.top
      this.simple.left = newRect.left
