<template>
  <div class="content-box">
    <h2 class="demo-title">Anchor </h2>
    <p class="demo-introduction"></p>

    <!-- DEMO -->
    <shn-el-demo-block :height="147" :title="''" id="anchor-simple">
      <template v-slot:demo>
        <p class="demo-code-en-p">see the right bar.</p>
        <p class="demo-code-cn-p"></p>
        <shn-anchor position="right">
          <shn-anchor-link href="anchor-simple" title=""/>
          <shn-anchor-link href="anchor-customize" title=""/>
        </shn-anchor>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(simple.code.html)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block :height="168" :title="''" id="anchor-customize">
      <template v-slot:demo>
        <shn-anchor customize>
          <shn-anchor-link href="api" title="API">
            <shn-button icon="shn-rotate-right" type="primary">API</shn-button>
          </shn-anchor-link>
        </shn-anchor>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(customize.code.html)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">Anchor API</h3>
      <shn-table-simple :data="anchorApi" :type="'demo'"/>
      <h3 class="demo-table-title">Link API</h3>
      <shn-table-simple :data="linkApi" :type="'demo'"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'shn-anchor-demo',
  data() {
    return {
      simple: {
        code: {
          html: `
          <shn-anchor position="right">
            <shn-anchor-link href="upload-img-simple" title=""/>
            <shn-anchor-link href="upload-img-cropper" title=""/>
          </shn-anchor>
          `
        }
      },
      customize: {
        code: {
          html: `
          <shn-anchor customize>
            <shn-anchor-link href="api" title="API">
              <shn-button icon="icon-rotate-right" type="primary">API</shn-button>
            </shn-anchor-link>
          </shn-anchor>
          `
        }
      },
      anchorApi: [
        {
          parameter: 'target',
          description: ',',
          dataTypes: 'String',
          optional: '',
          default: 'window'
        },
        {
          parameter: 'anchorClass',
          description: 'Class',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'customize',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'position',
          description: '',
          dataTypes: 'String',
          optional: 'left / right',
          default: 'right'
        }
      ],
      linkApi: [
        {
          parameter: 'href',
          description: ',domid',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'title',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: 'Title'
        }
      ]
    }
  },
  methods: {}
}
</script>
<style lang="scss" scoped>
</style>

