<template>
  <div class="content-box">
    <shn-backtop :visibilityHeight="300" />

    <h2 class="demo-title">vueDragResize /</h2>
    <p class="demo-introduction">vue-drag-resizevuevue 1.x2.x</p>

    <div class="demo-tip">
      <p>kirillmurashovgithub</p>
      <p>
        <a
          href="https://github.com/kirillmurashov/vue-drag-resize"
          target="_blank"
        >https://github.com/kirillmurashov/vue-drag-resize</a>
        <img
          alt
          class="github-img"
          href="https://github.com/kirillmurashov/vue-drag-resize"
          src="https://img.shields.io/github/stars/kirillmurashov/vue-drag-resize.svg?style=social&label=Star&maxAge=2592000"
        />
      </p>
    </div>
    <!--  -->
    <h3 class="demo-table-title"></h3>
    <p class="demo-introduction"> npm  webpack </p>
    <div class="demo-code-block">
      <p>npm i -s vue-drag-resize</p>
      <shn-divider />
      <p>main.js</p>
      <p>import VueDragResize from 'vue-drag-resize'</p>
      <p>Vue.component('vue-drag-resize', VueDragResize)</p>
    </div>

    <shn-anchor>
      <shn-anchor-link href="vue-draag-resize-simple" title="" />
      <shn-anchor-link href="vue-draag-resize-axis" title="" />
      <shn-anchor-link href="api" title="API" />
      <shn-anchor-link href="event" title="" />
    </shn-anchor>

    <!-- DEMO -->
    <shn-el-demo-block :height="648" :title="''" id="vue-draag-resize-simple">
      <template v-slot:demo>
        <div style="height:200px">
          <vue-drag-resize
            :h="100"
            :w="100"
            :x="20"
            :y="20"
            :z="1"
            v-on:dragging="resize"
            v-on:resizing="resize"
          >
            <div class="simple-block">
              <div>
                
                <p>{{ simple.top }}  {{ simple.left }}</p>
                <p>{{ simple.width }}  {{ simple.height }}</p>
              </div>
            </div>
          </vue-drag-resize>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(simple.code.html)}}</code>
        <code class="javascript">{{fCode(simple.code.javascript)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- DEMO -->
    <shn-el-demo-block :height="231" :title="''" id="vue-draag-resize-axis">
      <template v-slot:demo>
        <div style="height:200px;">
          <vue-drag-resize
            :h="100"
            :isResizable="false"
            :w="100"
            :x="20"
            :y="20"
            axis="x"
            parentLimitation
          >
            <div class="simple-block">
              <div>
                <p></p>
                <p></p>
                <p></p>
              </div>
            </div>
          </vue-drag-resize>
        </div>
      </template>
      <template v-slot:code>
        <code class="html">{{fCode(axis.code.html)}}</code>
      </template>
    </shn-el-demo-block>

    <!-- API -->
    <div id="api">
      <h3 class="demo-table-title">API</h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="api" :type="'demo'" />
    </div>

    <!--  -->
    <div id="event">
      <h3 class="demo-table-title"></h3>
      <P class="demo-table-introduction"></P>
      <shn-table-simple :data="event" :type="'demo'" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'vue-drag-resize-demo',
  data() {
    return {
      simple: {
        code: {
          html: `
          <vue-drag-resize :h="100" :w="100" :x="20" :y="20" v-on:dragging="resize" v-on:resizing="resize">
            <div class="simple-block">
              <div>
                
                <p>{{ top }}  {{ left }}</p>
                <p>{{ width }}  {{ height }}</p>
              </div>
            </div>
          </vue-drag-resize>
          `,
          javascript: `
          export default {
            data(){
              return{
                width: 100,
                height: 100,
                top: 20,
                left: 20
              }
            },
            methods:{
              resize(newRect) {
                this.simple.width = newRect.width
                this.simple.height = newRect.height
                this.simple.top = newRect.top
                this.simple.left = newRect.left
              }
            }
          }
          `
        },
        width: 100,
        height: 100,
        top: 20,
        left: 20
      },
      axis: {
        code: {
          html: `
          <vue-drag-resize :h="100" :w="100" :x="20" :y="20" axis="x" parentLimitation :isResizable="false">
            <div class="simple-block">
              <div>
                <p></p>
                <p></p>
                <p></p>
              </div>
            </div>
          </vue-drag-resize>
          `,
          javascript: ``
        }
      },
      api: [
        {
          parameter: 'isActive',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'isDraggable',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'isResizable',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'aspectRatio',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'w',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '200'
        },
        {
          parameter: 'h',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '200'
        },
        {
          parameter: 'minw',
          description: '',
          dataTypes: 'Number',
          optional: '>0',
          default: '50'
        },
        {
          parameter: 'minh',
          description: '',
          dataTypes: 'Number',
          optional: '>0',
          default: '50'
        },
        {
          parameter: 'x',
          description: 'left',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'y',
          description: 'top',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'z',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: 'auto'
        },
        {
          parameter: 'sticks',
          description: '',
          dataTypes: 'Array',
          optional: '',
          default: ''
        },
        {
          parameter: 'preventActiveBehavior',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'parentLimitation',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        },
        {
          parameter: 'parentW',
          description:
            'parentLimitation=true',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'parenth',
          description:
            ',parentLimitation=true',
          dataTypes: 'Number',
          optional: '',
          default: '0'
        },
        {
          parameter: 'axis',
          description: '',
          dataTypes: 'String',
          optional: 'x / y / both / none',
          default: 'both'
        },
        {
          parameter: 'dragHandle',
          description: 'classname',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'dragCancel',
          description: 'classname',
          dataTypes: 'String',
          optional: '',
          default: ''
        }
      ],
      event: [
        {
          parameter: '@clicked',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@activated',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@resizing',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@resizestop',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@dragging',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        },
        {
          parameter: '@dragstop',
          description: '',
          dataTypes: '',
          optional: '',
          default: ''
        }
      ]
    }
  },
  methods: {
    resize(newRect) {
      this.simple.width = newRect.width
      this.simple.height = newRect.height
      this.simple.top = newRect.top
      this.simple.left = newRect.left
    }
  }
}
</script>
<style lang="scss" scoped>
.simple-block {
  cursor: move;
  width: 100%;
  height: 100%;
  background: rgb(64, 158, 255);
  text-align: center;
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  border-radius: 5px;
}
</style>

